@use 'sass:map';

$alphas: (
    10: 0.1,
    20: 0.2,
    30: 0.3,
    40: 0.4,
    50: 0.5,
    60: 0.6,
    70: 0.7,
    80: 0.8,
    90: 0.9
);

/* =====================================================
   COLOR PALETTES (REVERSED SCALE)
   10 = light â†’ 100 = dark
   ===================================================== */

/* Gray */
$gray: (
  10  : #FFFFFF,
  20  : #F3F3F3,
  30  : #E5E5E5,
  40  : #C9C9C9,
  50  : #AEAEAE,
  60  : #939393, 
  70  : #5C5C5C,
  80  : #444444,
  90  : #2B2B2B,
  100 : #1A1A1A
);

/* Blue */
$blue: (
  10  : #EEF4FF,
  20  : #D8E6FE,
  30  : #AACBFF,
  40  : #78B0FD, 
  50  : #0176D3,
  60  : #0B5CAB,
  70  : #014486,
  80  : #032D60,
  90  : #03234D,
  100 : #001639
);

/* Cloud Blue */
$cloud-blue: (
  10  : #EAF5FE,
  20  : #CFE9FE,
  30  : #90D0FE, 
  40  : #0D9DDA,
  50  : #107CAD,
  60  : #05628A,
  70  : #084968,
  80  : #023248,
  90  : #0A2636,
  100 : #001A28
);

/* Indigo */
$indigo: (
   10 : #F1F3FB,
   20 : #E0E5F8,
   30 : #BEC7F6, 
   40 : #7F8CED,
   50 : #5867E8,
   60 : #3A49DA,
   70 : #2F2CB7,
   80 : #321D71,
   90 : #1F0974,
   100 : #200647
);

/* Cyan */
$cyan: (
  10 : #ECFEFF,
  20 : #CEFAFE,
  30 : #A2F4FD, 
  40 : #00D3F3,
  50 : #00B8DB,
  60 : #0092B8,
  70 : #007595,
  80 : #005F78,
  90 : #104E64,
  100: #053345
);

/* Purple */
$purple: (
  10 : #F6F2FB,
  20 : #ECE1F9,
  30 : #D7BFF2,
  40 : #C29EF1, 
  50 : #9050E9,
  60 : #7526E3,
  70 : #5A1BA9,
  80 : #401075,
  90 : #300B60,
  100: #240643
);

/* Red */
$red: (
  10 : #FEF1EE,
  20 : #FEDED8,
  30 : #FEB8AB, 
  40 : #FE5C4C,
  50 : #EA001E,
  60 : #BA0517,
  70 : #8E030F,
  80 : #640103,
  90 : #4A0C04,
  100: #300C01
);

/* Pink */
$pink: (
  10 :  #FEF0F3,
  20 :  #FDDDE3,
  30 :  #FDB6C5, 
  40 :  #FF538A,
  50 :  #E3066A,
  60 :  #B60554,
  70 :  #8A033E,
  80 :  #61022A,
  90 :  #4B0620,
  100: #370114
);

/* Hot Orange */
$hot-orange: (
  10  :  #FEF1ED,
  20  :  #FFDED5,
  30  :  #FEB9A5,
  40  :  #FF906E,
  50  :  #FF5D2D,
  60  :  #D83A00,
  70  :  #AA3001,
  80  :  #7E2600,
  90  :  #4A2413,
  100 :  #421604, 
);

/* Orange */
$orange: (
 10 :  #fdf4e8,
 20 :  #f8ddb9,
 30 :  #f4c78b,
 40 :  #f0b15c,
 50 :  #eb9a2d,
 60 :  #d28114,
 70 :  #a3640f,
 80 :  #74480b,
 90 :  #462b07, 
 100:  #170e02
);

/* Yellow */
$yellow: (
  10   :  #FBF3E0,
  20   :  #F9E3B6,
  30   :  #FCC003,
  40   :  #E4A201,
  50   :  #CA8501,
  60   :  #A86403,
  70   :  #8C4B02,
  80   :  #6F3400,
  90   :  #4F2100,
  100  :  #2E2204, 
);

/* Teal */
$teal: (
  10  :  #DEF9F3,
  20  :  #ACF3E4,
  30  :  #04E1CB,
  40  :  #01C3B3,
  50  :  #06A59A,
  60  :  #0B827C,
  70  :  #056764,
  80  :  #024D4C,
  90  :  #023434,
  100 :  #072825, 
);

/* Green */
$green: (
  10  :  #edf7ef,
  20  :  #c9e8d0,
  30  :  #a6d9b0,
  40  :  #82c991,
  50  :  #5eba71,
  60  :  #45a158,
  70  :  #367d44,
  80  :  #265931,
  90  :  #17361d,
  100 :  #08120a, 
);

$shade-colors: (
    "red" : $red, 
    "green": $green,
    "blue" : $blue,
    "cloud-blue" : $cloud-blue,
    "indigo" : $indigo,
    "cyan" : $cyan,
    "purple" : $purple,
    "pink" : $pink,
    "hot-orange" : $hot-orange,
    "orange" : $orange,
    "yellow" : $yellow,
    "teal" : $teal,
    "gray" : $gray, 
);



$base-colors: ( 
    "dark-green"    : green-70,
    "green"         : green-50,
    "light-green"   : green-10,

    "dark-blue"     : blue-70,
    "blue"          : blue-50,
    "light-blue"    : blue-10,

    "dark-orange"  : orange-70,
    "orange"       : orange-50,
    "light-orange" : orange-10,
     
    "dark-yellow"  : yellow-70,
    "yellow"       : yellow-50,
    "light-yellow" : yellow-10,

 
    "dark-red"      : red-70,
    "red"           : red-50,
    "light-red"     : red-10,

    ///---custom color

    "dark-info"     : blue-70,
    "info"          : blue-50,
    "light-info"    : blue-10,

     
    "dark-success"  : green-70,
    "success"       : green-50,
    "light-success" : green-10,

    "dark-error"    : red-70,
    "error"         : red-50,
    "light-error"   : red-10,

    "dark-warning"  : orange-70,
    "warning"       : orange-50,
    "light-warning" : orange-10,
 
    "dark-link"     : blue-70,
    "link"          : blue-50,
    "light-link"    : blue-10,

    "dark-disabled" : gray-70,
    "disabled"      : gray-50,
    "light-disabled": gray-10,

    "primary"        : gray-90,
    "white"          : gray-10,
    "secondary"      : gray-60,
    
);


