// Import configuration modules for design tokens and utilities
@use './config/spacing' as spacing;
@use './config/sizing' as sizing;
@use './config/shadows' as shadows;
@use './config/borders' as borders;
@use './config/typography' as typography;
@use './config/font_family' as ff;
// @use './config/colors' as colors;
@use './config/color' as colors;
@use './config/animation' as animation;
@use './config/g_prefix' as g;





// Root CSS custom properties for design tokens
:root {


    // Font sizes
    @each $name, $value in typography.$font-sizes {
        #{g.$prefix-font}-#{$name}: #{$value};
    }


    // Font weights
    @each $name, $value in typography.$font-weights {
        #{g.$prefix-font-weight}-#{$name}: #{$value};
    }

    // Font weights
    @each $name, $value in ff.$fontFamily {
        #{g.$prefix-font-family}-#{$name}: #{$name};
    }


    // Line heights
    @each $name, $value in typography.$line-heights {
        #{g.$prefix-line-height}-#{$name}: #{$value};
    }


    // Border radii
    @each $name, $value in borders.$borders-radius {
        #{g.$prefix-border-radius}-#{$name}: #{$value};
    }


    // Border widths
    @each $name, $value in borders.$borders-width {
        #{g.$prefix-border-width}-#{$name}: #{$value};
    }

    // Shadow values
    @each $name, $value in shadows.$shadows {
        #{g.$prefix-shadow}-#{$name}: #{$value};
    }

    // Sizing values
    @each $name, $value in sizing.$sizing {
        #{g.$prefix-sizing}-#{$name}: #{$value};
    }


    // Spacing values
    @each $key, $val in spacing.$spacing {
        #{g.$prefix-spacing}-#{$key}: #{$val};
    }


    // Animation values
    @each $name, $value in animation.$animation {
        #{g.$prefix-animation}-#{$name}: #{$value};
    }



    // Color palettes
    // @each $name, $palette in colors.$all-colors {
    //     @each $key, $value in $palette {
    //         #{g.$prefix-colors}-#{$key}: #{$value};
    //     }
    // }

    // @each $name, $value in colors.$neutral-tone {

    //     #{g.$prefix-colors}-#{$name}: #{$value};

    // }

    // Color palettes
    
    @each $name, $value in colors.$alphas {

        #{g.$prefix-colors}-black-#{$name}: rgba(0, 0, 0, $value);
        #{g.$prefix-colors}-white-#{$name}: rgba(255, 255, 255, $value);

    }

     @each $name, $palette in colors.$shade-colors {
        @each $key, $value in $palette {
            #{g.$prefix-colors}-#{$name}-#{$key}: #{$value};
        }
    }
}