
$font-name: 'Poppins';
$font-folder: 'Poppins';

@font-face {
    font-family: $font-name;
    src: url("web-fonts/#{$font-folder}/font_black.woff2") format('woff2'),
    url("web-fonts/#{$font-folder}/font_black.woff") format('woff');
    font-weight: 900;
    font-style: normal;
    font-display: swap;
}


@font-face {
    font-family: $font-name;
    src: url("web-fonts/#{$font-folder}/font_bold.woff2") format('woff2'),
    url("web-fonts/#{$font-folder}/font_bold.woff") format('woff');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: $font-name;
    src: url("web-fonts/#{$font-folder}/font_medium.woff2") format('woff2'),
    url("web-fonts/#{$font-folder}/font_medium.woff") format('woff');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: $font-name;
    src: url("web-fonts/#{$font-folder}/font_regular.woff2") format('woff2'),
    url("web-fonts/#{$font-folder}/font_regular.woff") format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: $font-name;
    src: url("web-fonts/#{$font-folder}/font_light.woff2") format('woff2'),
    url("web-fonts/#{$font-folder}/font_light.woff") format('woff');
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}


$fontFamily: (
    ff-light: 300,
    ff-normal: normal,
    ff-semibold: 500,
    ff-bold: 600,
    ff-bolder: 900,
);